import React from "react";
import styled from "styled-components";
import logo from "../../images/Galaxxi_Gaming_Logo_L.png";

const HeaderContainer = styled.div`
  height: 4rem;
  background-color: #6a0689;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
`;

const LogoContainer = styled.div`

`;

const Title = styled.div`
  cursor: default;
  font-size: 2rem;
  position: absolute;
  left: 14px;
  color: white;
  font-family: fantasy;
  top: 11px;
`;

const GalaxxiLogo = styled.img`
  min-width: 100px;
  width: 240px;
  position: absolute;
  right: 0;
  cursor: pointer;
`;

export const Header: React.FC = () => {

  return (
    <HeaderContainer>
      <Title>{"Flesh & Blood Spoilers"}</Title>
      <LogoContainer>
        <GalaxxiLogo src={logo} onClick={()=> window.open("https://www.galaxxigaming.com", "_blank", "noopener,noreferrer")}/>
      </LogoContainer>
    </HeaderContainer>
  )
}